import React from 'react'
import ReactDOM from 'react-dom'
import './App.css'
import Navigation from './NavigationBar/Navbar'
import TextTransition, { presets } from './TextTransition/TextTransition'

function App() {
  return (
    <div>
      <Navigation/>
      <h1 className="TeusdayHeader">
        What we do?
      </h1>
      <TextTransition/>
    </div>
  );
}

const app = document.getElementById('root');
ReactDOM.render(<Navigation/>, app);
ReactDOM.render(<TextTransition/>, app);
export default App;
