import React from "react";
import '../App.css'
import ReactTextTransition, { presets } from "react-text-transition";

const randomNumber = () => Math.floor(Math.random() * 9999999999 + 10000000000);

const designTexts = [" Graphic ", " UI/UX ", " Website ", " Branding "];
const developementTexts = [" Web ", " Android "];
 
export default class TextTransition extends React.Component {

    state = {
        number: randomNumber(),
        textIndex: 0,
        textFastIndex: 0,
        paragraphIndex: 0
    };
    
    componentDidMount() {
        setInterval(() => {
          this.setState({
            number: randomNumber(),
            textIndex: this.state.textIndex + 1,
            paragraphIndex: this.state.paragraphIndex + 1
          });
        }, 4000);
        setInterval(() => {
          this.setState({
            textFastIndex: this.state.textFastIndex + 1
          });
        }, 400);
    }


    render() {
        return (
            <div className="BodyTextBlack">
                <p>
                    We do 
                    <ReactTextTransition  className="BodyText"
                        text = {designTexts[this.state.textFastIndex % designTexts.length]}
                        spring = {presets.gentle}
                        style = {{ margin: "8px 4px" }}
                        inline
                        overflow
                    />
                    Design And
                    <ReactTextTransition className="BodyText"
                        text = {developementTexts[this.state.textFastIndex % developementTexts.length]}
                        spring = {presets.gentle}
                        style = {{ margin: "8px 4px" }}
                        inline
                        overflow
                    />
                    Developement
                </p>   
                <p>
                    
                </p>   
            </div>
        );
    }
}

